<template><div class="markdown-body"><h1>提示</h1>
<p><code v-pre="">mu-tooltip</code>  对于用户悬停在元素上时传递信息， 也可以使用 <code v-pre="">open.sync</code> 控制显示。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p><code v-pre="">mu-tooltip</code> 可以包裹任何元素</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;around\&quot; align-items=\&quot;center\&quot;&gt;\n    &lt;mu-tooltip content=\&quot;提示信息\&quot;&gt;\n      &lt;mu-button color=\&quot;primary\&quot;&gt;Tooltip&lt;/mu-button&gt;\n    &lt;/mu-tooltip&gt;\n    &lt;mu-tooltip content=\&quot;打印\&quot;&gt;\n      &lt;mu-button icon&gt;\n        &lt;mu-icon value=\&quot;print\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-button&gt;\n    &lt;/mu-tooltip&gt;\n    &lt;mu-tooltip content=\&quot;提示信息\&quot;&gt;\n      &lt;span&gt;文本提示信息&lt;/span&gt;\n    &lt;/mu-tooltip&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="around" align-items="center">
    <mu-tooltip content="提示信息">
      <mu-button color="primary">Tooltip</mu-button>
    </mu-tooltip>
    <mu-tooltip content="打印">
      <mu-button icon="">
        <mu-icon value="print"></mu-icon>
      </mu-button>
    </mu-tooltip>
    <mu-tooltip content="提示信息">
      <span>文本提示信息</span>
    </mu-tooltip>
  </mu-flex>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"around"</span> <span class="hljs-attr">align-items</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"提示信息"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>Tooltip<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-tooltip</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"打印"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">icon</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"print"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-tooltip</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"提示信息"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>文本提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-tooltip</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="wei-zhi"><a class="header-anchor" href="#wei-zhi" aria-hidden="true">¶</a> 位置</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;around\&quot; align-items=\&quot;center\&quot;&gt;\n    &lt;mu-tooltip :placement=\&quot;placement\&quot; content=\&quot;提示信息\&quot;&gt;\n      &lt;mu-button color=\&quot;primary\&quot;&gt;Tooltip&lt;/mu-button&gt;\n    &lt;/mu-tooltip&gt;\n    &lt;mu-tooltip :placement=\&quot;placement\&quot; content=\&quot;打印\&quot;&gt;\n      &lt;mu-button icon&gt;\n        &lt;mu-icon value=\&quot;print\&quot;&gt;&lt;/mu-icon&gt;\n      &lt;/mu-button&gt;\n    &lt;/mu-tooltip&gt;\n    &lt;mu-tooltip :placement=\&quot;placement\&quot; content=\&quot;提示信息\&quot;&gt;\n      &lt;span&gt;文本提示信息&lt;/span&gt;\n    &lt;/mu-tooltip&gt;\n  &lt;/mu-flex&gt;\n  &lt;p class=\&quot;demo-tooltip-title\&quot;&gt;弹出位置：&lt;/p&gt;\n  &lt;mu-row&gt;\n    &lt;mu-col :key=\&quot;p\&quot; v-for=\&quot;p in placements\&quot; span=\&quot;6\&quot; md=\&quot;4\&quot;&gt;\n      &lt;mu-radio v-model=\&quot;placement\&quot; :value=\&quot;p\&quot; :label=\&quot;p\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="around" align-items="center">
    <mu-tooltip :placement="placement" content="提示信息">
      <mu-button color="primary">Tooltip</mu-button>
    </mu-tooltip>
    <mu-tooltip :placement="placement" content="打印">
      <mu-button icon="">
        <mu-icon value="print"></mu-icon>
      </mu-button>
    </mu-tooltip>
    <mu-tooltip :placement="placement" content="提示信息">
      <span>文本提示信息</span>
    </mu-tooltip>
  </mu-flex>
  <p class="demo-tooltip-title">弹出位置：</p>
  <mu-row>
    <mu-col :key="p" v-for="p in placements" span="6" md="4">
      <mu-radio v-model="placement" :value="p" :label="p"></mu-radio>
    </mu-col>
  </mu-row>
</mu-container>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">justify-content</span>=<span class="hljs-string">"around"</span> <span class="hljs-attr">align-items</span>=<span class="hljs-string">"center"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-tooltip</span> <span class="hljs-attr">:placement</span>=<span class="hljs-string">"placement"</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"提示信息"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>Tooltip<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-tooltip</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-tooltip</span> <span class="hljs-attr">:placement</span>=<span class="hljs-string">"placement"</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"打印"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">icon</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"print"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-tooltip</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-tooltip</span> <span class="hljs-attr">:placement</span>=<span class="hljs-string">"placement"</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"提示信息"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>文本提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-tooltip</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tooltip-title"</span>&gt;</span>弹出位置：<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-col</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"p"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"p in placements"</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">md</span>=<span class="hljs-string">"4"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"placement"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"p"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"p"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-row</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="tooltip-props"><a class="header-anchor" href="#tooltip-props" aria-hidden="true">¶</a> Tooltip Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>显示内容，可使用 <code v-pre="">slot=&quot;content&quot;</code> 代替</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>open</td>
<td>是否显示</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>placement</td>
<td>Tooltip 的出现位置</td>
<td>String</td>
<td>top / top-start / top-end / bottom / bottom-start / bottom-end / left / left-start / left-end / right / right-start / right-end</td>
<td>—</td>
</tr>
<tr>
<td>tooltip-class</td>
<td>Tooltip 样式</td>
<td>String/Object/Array</td>
<td>—</td>
<td></td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
p.demo-tooltip-title {
  margin: 16px 0;
}
</style>
<script>
export default {
  data () {
    return {
      placements: [
        'top', 'top-start', 'top-end',
        'bottom', 'bottom-start', 'bottom-end',
        'left', 'left-start', 'left-end',
        'right', 'right-start', 'right-end'
      ],
      placement: 'bottom'
    }
  }
}
</script>


